import React, { Component } from 'react';
import { Redirect, Link } from "react-router-dom";
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/BuildControls';
import Modal from '../../components/UI/Modal/Modal';
import OrderSummery from '../../components/Burger/OrderSummery/OrderSummery';
import axios from 'axios';
import PopupModel from '../../components/OtherComponent/Model/PopupModel';


const INGREDIENT_PRICE = {
    salad: 0.4,
    bacon: 0.3,
    cheese: 0.2,
    meat: 0.7
};

class BurgerBuilder extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ingredients: {
                salad: 0,
                bacon: 0,
                cheese: 0,
                meat: 0
            },
            totalPrice: 4,
            purchaseble: false,
            purchaging: false,
            responseText: ""
        }
    }

    purchageHandler = () => {
        this.setState({ purchaging: true });
    }
    purchageCancelHandler = () => {
        this.setState({ purchaging: false });
    }
    closePopupHandler=()=>{
        this.props.history.push("/BillingAddress");
    }

    purchageContinueHandler = () => {
        let self = this;
        axios.post('http://10.60.4.208:3001/data', this.state)
            .then(function (response) {
                self.setState({ responseText: response.data });
                self.purchageCancelHandler();
               
            })
            .catch(function (error) {
                console.log(error);
            });
    }

    updateOrderButton = (ingredient) => {
        const sum = Object.keys(ingredient)
            .map(igKey => {
                return ingredient[igKey];
            })
            .reduce((sum, el) => {
                return sum + el;
            }, 0);

        this.setState({ purchaseble: sum > 0 });
    }

    addIngredientHandler = (type) => {
        const oldCount = this.state.ingredients[type];
        const updatedCount = oldCount + 1;
        const updatedIngredient = { ...this.state.ingredients };
        updatedIngredient[type] = updatedCount;
        const priceAddition = INGREDIENT_PRICE[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice + priceAddition;

        this.setState({ totalPrice: newPrice, ingredients: updatedIngredient });
        this.updateOrderButton(updatedIngredient);
    }


    removeIngredientHandler = (type) => {
        const oldCount = this.state.ingredients[type];
        if (oldCount <= 0) {
            return;
        }
        const updatedCount = oldCount - 1;
        const updatedIngredient = { ...this.state.ingredients };
        updatedIngredient[type] = updatedCount;
        const priceDeduction = INGREDIENT_PRICE[type];
        const oldPrice = this.state.totalPrice;
        const newPrice = oldPrice - priceDeduction;

        this.setState({ totalPrice: newPrice, ingredients: updatedIngredient });
        this.updateOrderButton(updatedIngredient);
    }

    render() {
        const disableInfo = { ...this.state.ingredients };
        for (let key in disableInfo) {
            disableInfo[key] = disableInfo[key] <= 0;
        }

        return (
            <div>
                <Modal show={this.state.purchaging} modelClose={this.purchageCancelHandler}>
                    <OrderSummery
                        conform={this.purchageCancelHandler}
                        continue={this.purchageContinueHandler}
                        totalPrice={this.state.totalPrice}
                        ingredients={this.state.ingredients} />
                </Modal>
                <Burger ingredients={this.state.ingredients} />
                <BuildControls
                    ingredientAdded={this.addIngredientHandler}
                    ingredientRemoved={this.removeIngredientHandler}
                    disabled={disableInfo}
                    price={this.state.totalPrice}
                    buttonState={this.state.purchaseble}
                    purchage={this.purchageHandler}
                />
            </div>
        );
    }
}

export default BurgerBuilder;


